	import uniCard from "@/components/uni-card/uni-card.vue"
	import uniList from '@/components/uni-list/uni-list.vue'
	import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
	import uniRate from '@/components/uni-rate/uni-rate.vue'
	import uniTag from '@/components/uni-tag/uni-tag.vue'
	import http from '../../store/http.js'
	export default {
		components: {
			uniList,
			uniListItem,
			uniRate,
			uniTag,
			uniCard
		},
		data() {
			return {
				current:0,
				items: [],
				items2:[],
				doctor:[],
				patient:[],
				sex:-1,
				name:null,
				tel:null,
				ID:null,
				visible:true,
			}
		},filters: {
            formatDate: function (value) {
                let date = new Date(value);
                let y = date.getFullYear();
                let MM = date.getMonth() + 1;
                MM = MM < 10 ? ('0' + MM) : MM;
                let d = date.getDate();
                d = d < 10 ? ('0' + d) : d;
                return y + '-' + MM + '-' + d;
            }
        },methods:{
			formSubmit: function(e) {
				if(this.sex==null || this.name==null || this.tel==null || this.ID==null){
					uni.showToast({
						title: '信息未填写完整或填写不正确',
						duration: 2000
					});
				}else{
					this.patient.sex=this.sex
					this.patient.realName=this.name
					this.patient.identityId=this.ID
					this.patient.phoneNumber=this.tel
					let opts={
	                 	url: '/wechat/updatePatient',
	                    method: 'POST'
		             };
				    http.httpTokenRequest(opts,this.patient).then(res => {
				
						this.visible=true
						this.patient.confirm=true
						
		            },error => {
						uni.showToast({
							title: '网络错误，请稍后重试',
							duration: 2000,
							icon:'none'
						});
					})    
				}
			},
			radioChange: function(evt) {
				this.sex=evt.target.value
			},
			async getSchdule(){
			let opts={
                 	url: '/wechat/getSchedule/'+this.doctor.id,
                    method: 'GET'
             };
		    http.httpTokenRequest(opts, null).then(res => {
				this.items=res.data.data
				for(var i=0;i<this.items.length;i++){
					this.items[i].value=this.items[i].id
				}	
            },error => {
				uni.showToast({
					title: e.data,
					duration: 2000,
					icon:'none'
				});
			})    
				
			},reserve(item){
				if(this.patient.confirm==true){
					this.actualReverse(item)
					return
				}
				let opts={
	                 	url: '/wechat/getPatient',
	                    method: 'GET'
	             };
			    http.httpTokenRequest(opts, null).then(res => {
					this.patient=res.data.data
					if(this.patient.confirm==true){
						this.actualReverse(item)
					}else{
						this.sex=this.patient.sex
						this.name=this.patient.realName
						this.ID=this.patient.identityId
						this.tel=this.patient.phoneNumber
						this.visible=false
					}
	            },error => {
					uni.showToast({
						title: e.data,
						duration: 2000,
						icon:'none'
					});
				})    
			},actualReverse(item){
				let opts={
	                 	url: '/registration/preRegistration/'+item.id,
	                    method: 'POST'
	             };
			    http.httpTokenRequest(opts, null).then(res => {
					if(res.data.code!=100){
						uni.showToast({
							title: res.data.msg,
							duration: 2000,
							icon:'none'
						});
					}else{
						this.visible=true
						uni.showToast({
							title: '预约成功',
							duration: 1500
						});
					}
	            },error => {
					uni.showToast({
						title: '网络错误，请稍后重试',
						duration: 2000,
						icon:'none'
					});
				})
			}
		},
		onLoad(options) {
			this.doctor = JSON.parse(options.doctor)	
			this.getSchdule()
		}
	}
